* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.header {
  width: 100%;
  height: 70px;
  background-color: #4f4fa6;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form {
  margin: 0;
  padding: 0;
  position: relative;
}

.search-form input {
  width: 300px;
  height: 40px;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
}

.search-form button {
  width: 70px;
  height: 100%;
  cursor: pointer;
  background-color: #ca752b;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 0 5px 5px 0;
  position: absolute;
  top: 0;
  right: 0;
}

.search-form button:hover {
  background-color: #e9a9a9;
}

.gallery {
  flex-wrap: wrap;
  padding: 85px 10px 40px;
  display: flex;
}

.photo-card {
  width: calc(25% - 22.5px);
  max-height: 292px;
  margin-bottom: 30px;
  margin-right: 30px;
  padding: 0;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.photo-card:nth-child(4n) {
  margin-right: 0;
}

.photo-card:nth-last-child(-n+4) {
  margin-bottom: 0;
}

.photo-thumb {
  width: 100%;
  height: 212px;
}

.image {
  width: 100%;
  height: 100%;
  display: block;
}

.info {
  margin-left: 5px;
  margin-right: 5px;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item:not(:last-child) {
  margin-right: 15px;
}

.load-more {
  width: 150px;
  height: 40px;
  text-transform: uppercase;
  color: #fafafa;
  cursor: pointer;
  background-color: #4f4fa6;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-bottom: 20px;
  font-size: 17px;
  position: relative;
  left: 45%;
}

.load-more:hover {
  background-color: #ca752b;
}

.load-more.is-hidden {
  display: none;
}

